* {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  font-family: "Times New Roman", Times, serif;
  background-color: aqua;
}

a {
  text-decoration: none;
  display: inline-block;
}

.flex {
  display: flex;

  &-jcc {
    justify-content: center;
  }

  &-tac {
    text-align: center;
  }

  &-aic {
    align-items: center;
  }

  &-jcsb {
    justify-content: space-between;
  }

  &-jce {
    justify-content: end;
  }
}

.quote {
  &-box {
    width: 300px;
    margin: 0 auto;
    border: 1px solid aquamarine;
    background-color: #fff;
  }
  &-text {
    #text {
      line-height: 1.5;
      font-size: 1.5em;
    }
  }

  &-author {
    #author {
      font-size: 1rem;
    }
  }

  &-text,
  &-author,
  &-buttons {
    margin: 1rem;
  }
}

button {
  font-family: "Times New Roman", Times, serif;
  font-size: 16px;
}

.btn {
  padding: 5px;
  background-color: aqua;
  color: black;
  border: 1px solid aqua;
  cursor: pointer;
}
